<template>
  <v-app>
      <app-navbar/>
      <v-main>
        <v-container class="pa-0" fluid>
          <router-view></router-view>
        </v-container>
      </v-main>
      <alert-snackbar v-if="alert.state" :text="alert.text" :type="alert.type"/>
      <v-overlay :value="loading">
        <v-progress-circular
          indeterminate
          size="64"
        ></v-progress-circular>
      </v-overlay>
  </v-app>
</template>

<script>
import { mapMutations } from 'vuex'
import AlertSnackbar from './components/AlertSnackbar.vue'
import AppNavbar from './components/AppNavbar.vue'

export default {
  components: { AppNavbar, AlertSnackbar },
  name: 'app',
  computed:{
    loading(){
      return this.$store.state.loading
    },
    alert(){
      return this.$store.state.alertModule.alert
    },
    currentRoute(){
      return this.$route.name
    }
  },
  watch:{
    currentRoute(){
      this.setLoading(true)
      setTimeout(() => {
        this.setLoading(false)
      }, 500);
    }
  },
  methods:{
    ...mapMutations(['setAlert', 'setUser', 'setLoading'])
  },
  created(){
    this.setLoading(true)
  },
  mounted(){
    this.setLoading(false)
  }
}

</script>

